import{html}from"lit";import{i18next}from"@openremote/or-translate";export class ModalService{confirmation(e,t=i18next.t("flowEditor","Flow editor"),i=i18next.t("areYouSure","Are you sure?")){this.element.content=html`<confirmation-dialog 
        .question = "${i}"
        @agreed="${()=>{e&&e(),this.element.close()}}"
        @disagreed="${this.element.close}"
        ></confirmation-dialog>`,this.element.header=t,this.element.open()}notification(e,t,i=i18next.t("ok","OK")){this.element.content=html`<notification-dialog 
        .message = "${t}"
        @closed = "${this.element.close}"
        ></notification-dialog>`,this.element.header=e,this.element.open()}anything(e,t){this.element.content=t,this.element.header=e,this.element.open()}}